// ---------------------------START OPTION G---------------------------

.content-front {
  background: $beige-custom;
  height: 100%;
  width: 100%;

  .title {
    font-family: Graphik, sans-serif;
    font-size: 50px;
    line-height: 64px;
    font-weight: bold;
  }

  .subtitle {
    color: $primary;
  }

  .p-responsive {
    padding-left: 150px;
  }

  .content-img-home {
    height: 100vh;

    &-flip {
      display: none;
    }

    .img-home {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
    }
  }
}

@media (max-width: 800px) {
  .content-front {
    .p-responsive {
      padding-left: 20px;
    }

    .content-img-home {
      .img-home {
        width: 70%;
      }
    }
  }
}

@media (max-width: 600px) {
  .content-front {

    background: white;

    .p-responsive {
      padding-left: 20px;
    }

    .title {
      margin-top: 80px;
      font-size: 30px;
      line-height: 44px;
    }

    .content-img-home {
      height: 80vh;
      margin-top: 100px;


      .img-home {
        display: none;
      }

      &-flip {
        display: block;
        width: 100%;
        height: 101%;
        border-bottom: 300px solid $primary;

        &-green {
          overflow: hidden;
          position: absolute;
          width: 90%;
          height: 380px;
          left: 5%;
          background: rgb(159, 186, 196);

          .img-home-flip {
            height: 175%;
            position: absolute;
            top: -48%;
            left: -18%;
          }
        }
      }
    }

    .your-case-logo {
      left: 16px;
      top: 16px;
    }
  }
}


.your-case-logo {
  position: absolute;
  left: 32px;
  top: 30px;
}

