.content-cases {
  background: $primary;
  height: auto;
  padding: 20px 80px 120px 80px !important;
  position: relative;
  color: white;
  margin: auto;
  transition: width .4s;

  .icon-on-display {
    display: none !important;
  }

  .item-route {
    background: $primary-light;
    height: 86px;
    border-radius: 4px;
    transition: background .4s;

    h6 {
      transition: color .4s;
    }
  }

  .item-route:hover {
    background: #00805f;
    text-decoration: none;

    h6 {
      color: white;
    }
  }

}

@media (max-width: 600px) {

  .content-cases {
    .item-on-display {
      display: none !important;
    }

    .icon-on-display {
      display: flex !important;
    }

    .item-route {
      height: 164px;
      width: 164px;
      background: $beige-custom;
      border-radius: 0;
      color: $primary-light !important;
    }
  }
}


.content-cases:after {
  content: "";
  position: absolute;
  width: 100%;
  top: -50px;
  left: 0;
  height: 60px;
  z-index: 10;
  background: $primary;
  transition: top .4s, height .4s;
}


@media (max-width: 600px) {
  .content-cases {
    width: 100% !important;
    padding: 20px 20px 120px 20px !important;
  }
  .content-cases:after {
    content: "";
    height: 0;
    background: transparent;
    top: 0;
  }
  .container-fluid {
    padding: 0;
  }


}


//-----------------------------STEPS--------------------------------
.content-steps {
  .font-num {
    font-size: 104px;
    font-weight: bold;
    color: $orange;
  }
}

.blue-background {
  background: $tertiary;
  height: 80vh;
  width: 50%;
  position: relative;
  margin: auto;
}

.img-steps {
  height: 80%;
  position: absolute;
  top: 10%;
  right: 0;
  background: transparent;

  img {
    height: 100%;
  }
}


@media (max-width: 991px) {
  .content-steps {

    text-align: center;
  }
  .img-steps {
    width: 100%;
    top: -80%;
    text-align: center;
    height: 200%;
  }
  .blue-background {
    margin-top: 50%;
    width: 100%;
    height: 60vh;
  }
}

//-----------------------------FOOTER--------------------------------

footer {
  height: auto;
  background: $primary;
  color: white;
  padding-bottom: 50px;
}




